@use "../global" as g;

/*!
layout > container
------------------------------
*/

.container {
    padding-left: min((20/320)*100vw,30px);
    padding-right: min((20/320)*100vw,30px);
    max-width: 100%;
    width: 100%;
    margin:0 auto;
    @include g.mq('md'){
        padding-left: g.vw(20);
        padding-right: g.vw(20);
        width: g.$width-content;
    }
    @include g.mq('xxl') {
        padding-left: 20px;
        padding-right: 20px;
        width: 1200px;
    }
}
