@use '../../global'as g;

/*!
page > common > common
------------------------------
*/
#wrapper {
    overflow: hidden;
}

.pc {
    display: none;
}

body.overflow {
    overflow: hidden;
}

a,
a *,
div,
div * {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
@include g.mq('md') {
    .sp {
        display: none;
    }

    .pc {
        display: inherit;
    }

    html {
        overflow-y: overlay;
        // overflow-y: auto;
        /* Fix not show on Firefox */
        scrollbar-color: #00ADC6 #eee;
        scrollbar-width: thin;
    }

    html.overflow {
        overflow-y: hidden;
    }

    /* For Chrome and other browsers except Firefox */
    body::-webkit-scrollbar {
        width: 0.3em;
        height: 0.3em;
        background-color: #eee;
        z-index: 1;
    }

    body::-webkit-scrollbar-thumb {
        background-color: #00ADC6;
    }
}
