@use "../global"as g;

/*!
component > header
------------------------------
*/

.header {
    position: fixed;
    z-index: 1024;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba($color: #FFF, $alpha: .8);

    img {
        vertical-align: initial;
    }

    //! ---> header_container
    &_container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &_logo {
        width: g.vw(203);
    }

    &_info {
        display: flex;

        .nav_link {
            height: g.vw(40);
            @include g.fonts(0, 14, 500, 14, 0, #737882);
            font-size: g.vw(14);
            background-color: #F7F7F7;

            display: flex;
            align-items: center;
            justify-content: center;

            icon-image {
                margin-right: g.vw(6);
            }
        }

        .project_story {
            width: g.vw(212);
            border-radius: 999px;

            icon-image {
                width: g.vw(14);
            }
        }

        .language {
            font-family: g.$f_montserrat;
            width: g.vw(180);
            background-color: transparent;

            padding-right: g.vw(14);

            icon-image {
                width: g.vw(16);
            }
        }

        .user {
            width: g.vw(220);

            icon-image {
                width: g.vw(16);
            }
        }
    }

    .first {
        height: g.vw(90);
        padding-left: g.vw(30);
        padding-right: g.vw(30);


        .l {}

        .r {}


    }

    .second {
        border-top: 1px solid #EFECEC;
        border-bottom: 1px solid #EFECEC;
    }
}

.nav {
    width: 100%;
    display: flex;
    position: relative;

    &_mask {
        width: 100vw;
        height: 100vh;

        position: fixed;
        z-index: 1005;
        top: g.vw(211);
        left: 0;

        background-color: #44474E;

        mix-blend-mode: multiply;

        visibility: hidden;
        opacity: 0;
        transition: visibility 300ms ease, opacity 200ms linear 100ms;

        &.show {
            opacity: 1;
            visibility: visible;
        }
    }

    .inquiry {
        width: g.vw(200);
        height: g.vw(60);
        @include g.fonts(0, 16, 700, 22, 0, #FFF);
        font-size: g.vw(16);
        background-color: #1D4156;

        display: flex;
        align-items: center;
        justify-content: center;

        margin-left: g.vw(103);
    }
}

.call_center {
    background: rgb(176, 192, 108);
    background: linear-gradient(45deg, rgba(176, 192, 108, 1) 0%, rgba(59, 165, 40, 1) 100%);
}

.research {
    background: rgb(102, 172, 192);
    background: linear-gradient(45deg, rgba(102, 172, 192, 1) 0%, rgba(44, 104, 201, 1) 100%);
}

.inside_sales {
    background: rgb(186, 172, 124);
    background: linear-gradient(45deg, rgba(186, 172, 124, 1) 0%, rgba(183, 137, 41, 1) 100%);
}

.navbar {
    &_menu {
        display: flex;
    }

    .menu_item {
        width: g.vw(237);

        .nav_link {
            width: 100%;
            height: g.vw(60);
            @include g.fonts(0, 16, 700, 16, 0, #FFF);
            font-size: g.vw(16);

            display: flex;
            align-items: center;
            justify-content: center;

            icon-image {
                width: g.vw(20);
                margin-right: g.vw(8);
            }
        }
    }

    &_submenu {
        display: flex;
        align-items: center;
        margin-left: auto;

        &_item {
            padding-left: g.vw(25);
            padding-right: g.vw(25);

            .nav_link {
                height: g.vw(60);
                display: flex;
                align-items: center;

                @include g.fonts(0, 16, 500, 22, 0, #44474E);
                font-size: g.vw(16);
            }

            .nav_link_blank {
                img {
                    width: g.vw(20);
                    height: g.vw(20);
                    margin-right: 5px;
                }
            }
        }
    }
}

.research_menu_pc {
    @include g.screen_min(768px) {
        background: rgb(102, 172, 192);
        background: linear-gradient(57deg, rgba(102, 172, 192, 1) 15%, rgba(50, 112, 200, 1) 35%);
    }
}

.normal_menu {
    background-color: rgba($color: #FFF, $alpha: .85);
}

.drop_menu {
    width: 100%;

    position: absolute;
    z-index: 0;
    left: 0;
    top: 60px;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: all 300ms ease;


    transition: visibility 300ms ease, opacity 200ms linear 100ms;
    overflow: hidden;

    &_lists {
        display: flex;
        align-items: center;
        transition: all 300ms ease;
        transform: translateY(-100%);
    }

    &_item {
        height: g.vw(60);
        @include g.fonts(0, 16, 700, 32, 0, #FFF);
        font-size: g.vw(16);
        margin-left: g.vw(25);
        margin-right: g.vw(25);
    }

    @include g.pchover {
        visibility: hidden;
        opacity: 0;
    }

}

.has_drop_menu {
    >.nav_link {
        position: relative;

        &::after {
            content: '';
            width: g.vw(92);
            height: 4px;

            position: absolute;
            bottom: 0;
            left: 50%;

            background-color: #FFF;
            transition: all 300ms linear;

            transform: translateX(-50%) scale(0, 1);
        }
    }

    &.other_bottom_line_color {
        >.nav_link {
            &::after {
                width: g.vw(64);
                background-color: #44474E;
            }
        }
    }

    @include g.pchover {

        &:hover {
            >.nav_link {

                &::after {
                    transform: translateX(-50%) scale(1, 1);
                }
            }

            .drop_menu {
                opacity: 1;
                visibility: visible;
            }

            .drop_menu_lists {
                transform: translateY(0);
            }
        }
    }
}

@include g.screen_min(1601px) {

    .header {

        &_logo {
            width: 203px;
        }

        &_info {
            .nav_link {
                height: 40px;
                font-size: g.rem(14);

                icon-image {
                    margin-right: 6px;
                }
            }

            .project_story {
                width: 212px;

                icon-image {
                    width: 14px;
                }
            }

            .language {
                width: 180px;
                padding-right: 14px;

                icon-image {
                    width: 16px;
                }
            }

            .user {
                width: 220px;

                icon-image {
                    width: 16px;
                }
            }
        }

        .first {
            height: 90px;
            padding-left: 30px;
            padding-right: 30px;

        }

    }

    .nav {
        &_mask {
            top: 211px;
        }

        .inquiry {
            width: 200px;
            height: 60px;
            font-size: g.rem(16);
            margin-left: 103px;
        }
    }

    .navbar {

        .menu_item {
            width: 237px;

            .nav_link {
                height: 60px;
                font-size: g.rem(16);

                icon-image {
                    width: 20px;
                    margin-right: 8px;
                }
            }
        }


        &_submenu {
            height: 60px;

            &_item {
                padding-left: 25px;
                padding-right: 25px;

                .nav_link {
                    font-size: g.rem(16);
                }
            }
        }
    }

    .drop_menu {

        &_item {
            height: 60xp;
            font-size: g.rem(16);
        }

    }

    .has_drop_menu {
        >.nav_link {
            &::after {
                width: 92px;
            }
        }

        &.other_bottom_line_color {
            >.nav_link {
                &::after {
                    width: 64px;
                }
            }
        }
    }
}

@include g.screen_max(767px) {
    .icon {
        &_plus {
            width: g.vw(20, 375);
            height: g.vw(20, 375);
            margin-left: auto;
            position: relative;

            &::after,
            &::before {
                content: '';
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }


            &::before {
                width: 1px;
                height: g.vw(16, 375);
            }

            &::after {
                width: g.vw(16, 375);
                height: 1px;
            }

            &__white {

                &::after,
                &::before {
                    background-color: #fff;
                }
            }

            &__dark {

                &::after,
                &::before {
                    background-color: #9BA6AF;
                }
            }
        }
    }

    .header {
        &_logo {
            width: g.vw(112, 375);

        }

        &_info {
            align-items: center;
        }

        .first {
            height: g.vw(62, 375);
            padding-left: g.vw(20, 375);
            padding-right: g.vw(20, 375);

            .r {}

            .nav_link_inquiry {
                width: g.vw(80, 375);
                height: g.vw(30, 375);
                display: flex;
                align-items: center;
                justify-content: center;

                background-color: #1D4156;
                @include g.fonts(0, 10, 700, 32, 0, #FFF);
                font-size: g.vw(10, 375);
                transition: all 300ms linear;

                &.hidden {
                    opacity: 0;
                }
            }
        }

        .second {
            position: fixed;
            z-index: -1;
            bottom: -1px;
            left: 0;
            width: 100%;
            height: calc(100% - g.vw(60, 375));

            overflow: auto;
            background-color: #fff;
            visibility: hidden;
            opacity: 0;
            transition: visibility 300ms ease, opacity 200ms linear 100ms;

            &.show {
                visibility: visible;
                opacity: 1;
            }
        }
    }

    .nav {
        display: block;
        padding-bottom: g.vw(20, 375);

        &_toggler {
            width: g.vw(20, 375);
            height: g.vw(20, 375);
            position: relative;

            margin-left: g.vw(30, 375);

            &::before,
            &::after {
                content: '';
                width: g.vw(18, 375);
                height: 2px;

                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                background-color: #00ADC6;

                transition: 300ms linear;
            }

            &::before {
                top: g.vw(6, 375);
            }

            &::after {
                top: g.vw(14, 375);
            }

            &.active {

                &::before,
                &::after {
                    top: 50%;
                }

                &::before {
                    transform: translate(-50%, -50%) rotate(-45deg);
                }

                &::after {
                    transform: translate(-50%, -50%) rotate(45deg);
                }
            }
        }

        &_mask {
            display: none;
        }

        .language {
            display: flex;
            align-items: center;
            @include g.fonts(g.$f_montserrat, 14, 500, 32, 0, #737882);
            font-size: g.vw(14, 375);

            padding-left: g.vw(20, 375);
            margin-top: g.vw(10, 375);

            .icon_image {
                width: g.vw(20, 375);
                height: g.vw(20, 375);

                display: block;
                object-fit: none;

                margin-right: 5px;
            }
        }

        .inquiry {
            width: g.vw(335, 375);
            height: g.vw(60, 375);
            font-size: g.vw(16, 375);

            margin-top: g.vw(20, 375);
            margin-left: auto;
            margin-right: auto;
        }
    }

    .navbar {
        &_menu {
            display: block;
        }

        .menu_item {
            width: 100%;

            .nav_link {
                height: g.vw(56, 375);
                font-size: g.vw(16, 375);
                justify-content: flex-start;

                padding-left: g.vw(20, 375);
                padding-right: g.vw(20, 375);

                icon-image {
                    width: g.vw(24, 375);
                    height: g.vw(24, 375);
                    margin-right: g.vw(8, 375);
                }
            }
        }

        &_submenu {
            display: block;

            &_item {

                border-bottom: 1px solid rgba($color: #E5E5E5, $alpha: .48);

                .nav_link {
                    height: g.vw(56, 375);
                    font-size: g.vw(14, 375);
                    padding-left: g.vw(20, 375);
                    padding-right: g.vw(20, 375);
                }

                .nav_link_blank {
                    flex-direction: row-reverse;

                    img {
                        width: g.vw(20, 375);
                        height: g.vw(20, 375);
                        margin-right: 5px;
                        margin-left: auto;
                    }
                }
            }
        }
    }

    .normal_menu {
        background-color: #F5FAFB;
    }

    .drop_menu {

        position: relative;
        top: 0;

        display: block;

        &_lists {
            display: block;
            transform: none;

        }

        &_item {
            height: g.vw(56, 375);
            font-size: g.vw(16, 375);
            margin-left: 0;
            margin-right: 0;
            padding-left: g.vw(20, 375);
            border-top: 1px solid rgba($color: #E5E5E5, $alpha: .48)
        }

    }

    .has_drop_menu {
        &.active {
            >.nav_link {
                .icon_plus::before {
                    opacity: 0;
                }
            }
        }
    }
}